.play_button {
  width: 200px;
  height: 200px;
  -webkit-transition: 300ms opacity ease-in-out, 200ms -webkit-filter ease-in-out;
  transition: 300ms opacity ease-in-out, 200ms -webkit-filter ease-in-out;
  transition: 200ms filter ease-in-out, 300ms opacity ease-in-out;
  transition: 200ms filter ease-in-out, 300ms opacity ease-in-out, 200ms -webkit-filter ease-in-out; }
  .play_button .background {
    fill: #000; }
  .play_button .network {
    fill: #fff; }
    .play_button .network.glow {
      opacity: 0; }
  .play_button .button {
    fill: #000; }
  .play_button .button {
    fill: #000;
    -webkit-transition: 120ms -webkit-transform ease-in;
    transition: 120ms -webkit-transform ease-in;
    transition: 120ms transform ease-in;
    transition: 120ms transform ease-in, 120ms -webkit-transform ease-in; }
  .play_button.exiting, .play_button.hidden {
    opacity: 0; }
  .play_button.flicker, .play_button.flicker:hover {
    -webkit-filter: blur(5px);
            filter: blur(5px); }
  .play_button.flicker .network {
    -webkit-animation: flicker 7s linear infinite;
            animation: flicker 7s linear infinite; }
  .play_button.blur {
    -webkit-filter: blur(10px);
            filter: blur(10px); }
  .play_button.spin {
    opacity: 0.9;
    -webkit-animation: spin 1.5s linear infinite;
            animation: spin 1.5s linear infinite; }
    .play_button.spin .network {
      opacity: 0.7; }
  .play_button.pulse {
    -webkit-animation: pulse-opacity 2s linear infinite;
            animation: pulse-opacity 2s linear infinite; }
    .play_button.pulse.spin {
      -webkit-animation: spin 1.5s linear infinite, pulse-opacity 2s linear infinite;
              animation: spin 1.5s linear infinite, pulse-opacity 2s linear infinite; }
  .play_button.error {
    -webkit-animation: none;
            animation: none;
    -webkit-transform: rotate(-90deg) scale(0.8);
            transform: rotate(-90deg) scale(0.8);
    -webkit-transition: opacity 0.8s ease-in-out, -webkit-transform 0.8s ease-in-out;
    transition: opacity 0.8s ease-in-out, -webkit-transform 0.8s ease-in-out;
    transition: opacity 0.8s ease-in-out, transform 0.8s ease-in-out;
    transition: opacity 0.8s ease-in-out, transform 0.8s ease-in-out, -webkit-transform 0.8s ease-in-out; }
    .play_button.error .network {
      -webkit-transition: all 150ms ease-in-out;
      transition: all 150ms ease-in-out;
      -webkit-animation: pulse-opacity 2s linear infinite;
              animation: pulse-opacity 2s linear infinite;
      fill: #b11623;
      opacity: 0.8; }
  .play_button.clickable {
    cursor: pointer; }
    .play_button.clickable:hover, .play_button.clickable:focus {
      -webkit-filter: blur(0);
              filter: blur(0);
      -webkit-animation: stop;
              animation: stop; }
      .play_button.clickable:hover .network, .play_button.clickable:focus .network {
        -webkit-animation: none;
                animation: none; }
    .play_button.clickable .button:hover,
    .play_button.clickable .button:focus {
      -webkit-transform: scale(1.06) translate(-3%, -3%);
              transform: scale(1.06) translate(-3%, -3%); }
    .play_button.clickable .button:active {
      -webkit-transition: 60ms -webkit-transform ease-in;
      transition: 60ms -webkit-transform ease-in;
      transition: 60ms transform ease-in;
      transition: 60ms transform ease-in, 60ms -webkit-transform ease-in;
      -webkit-transform: scale(0.94) translate(3%, 3%);
              transform: scale(0.94) translate(3%, 3%); }

.play_button_wrap {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -100px 0 0 -100px; }
  .play_button_wrap.hidden {
    display: none; }

.play_button_wrap.hovering.disabled {
  -webkit-animation: shake-no 2s linear 1;
          animation: shake-no 2s linear 1; }

.play_button_wrap.hovering:not(.disabled) {
  -webkit-animation: shake-yes 1.5s linear 1;
          animation: shake-yes 1.5s linear 1; }

